'use client'

import { useState } from 'react'
import Link from 'next/link'
import { PageHeader } from '@/components/ui/PageHeader'

const REPUTATION_TABS = [
  { id: 'overview', label: 'Overview', href: '/sub-account/reputation' },
  { id: 'reviews', label: 'Reviews', href: '/sub-account/reputation/reviews' },
  { id: 'requests', label: 'Review Requests', href: '/sub-account/reputation/requests' },
  { id: 'listings', label: 'Business Listings', href: '/sub-account/reputation/listings' },
  { id: 'widgets', label: 'Widgets', href: '/sub-account/reputation/widgets' },
  { id: 'settings', label: 'Settings', href: '/sub-account/reputation/settings' },
]

interface Widget {
  id: string
  name: string
  type: 'badge' | 'carousel' | 'grid' | 'list' | 'floating'
  description: string
  preview: string
  active: boolean
  views: number
  clicks: number
}

const mockWidgets: Widget[] = [
  { id: 'widget-1', name: 'Review Badge', type: 'badge', description: 'Display your overall rating with a compact badge', preview: '‚≠ê', active: true, views: 12450, clicks: 890 },
  { id: 'widget-2', name: 'Review Carousel', type: 'carousel', description: 'Rotating carousel of your best reviews', preview: 'üé†', active: true, views: 8920, clicks: 456 },
  { id: 'widget-3', name: 'Review Grid', type: 'grid', description: 'Grid layout showing multiple reviews at once', preview: 'üì±', active: false, views: 3200, clicks: 120 },
  { id: 'widget-4', name: 'Review List', type: 'list', description: 'Vertical list of recent reviews', preview: 'üìã', active: false, views: 1890, clicks: 78 },
  { id: 'widget-5', name: 'Floating Badge', type: 'floating', description: 'Floating badge that follows user scroll', preview: 'üéØ', active: true, views: 5670, clicks: 234 },
]

export default function WidgetsPage() {
  const [widgets, setWidgets] = useState<Widget[]>(mockWidgets)
  const [showCreateModal, setShowCreateModal] = useState(false)
  const [showCodeModal, setShowCodeModal] = useState(false)
  const [selectedWidget, setSelectedWidget] = useState<Widget | null>(null)
  const [widgetName, setWidgetName] = useState('')
  const [widgetType, setWidgetType] = useState<Widget['type']>('badge')

  const handleToggleActive = (id: string) => {
    setWidgets(prev => prev.map(w => w.id === id ? { ...w, active: !w.active } : w))
  }

  const handleShowCode = (widget: Widget) => {
    setSelectedWidget(widget)
    setShowCodeModal(true)
  }

  const generateEmbedCode = (widget: Widget) => {
    return `<!-- Risivo Review Widget: ${widget.name} -->
<script src="https://cdn.risivo.com/widgets/v1/reviews.js"></script>
<div id="risivo-reviews-${widget.id}" 
     data-widget-type="${widget.type}"
     data-theme="light"
     data-rating-color="#F59E0B"
     data-max-reviews="5">
</div>
<script>
  RisivoReviews.init({
    widgetId: '${widget.id}',
    apiKey: 'YOUR_API_KEY'
  });
</script>`
  }

  const totalViews = widgets.reduce((sum, w) => sum + w.views, 0)
  const totalClicks = widgets.reduce((sum, w) => sum + w.clicks, 0)
  const activeWidgets = widgets.filter(w => w.active).length

  return (
    <div className="space-y-6 pb-8">
      <div className="flex-none bg-white border-b border-slate-200 -mx-4 sm:-mx-6 lg:-mx-8 -mt-4 sm:-mt-6 lg:-mt-8 px-4 sm:px-6 lg:px-8 pt-4 sm:pt-6 lg:pt-8 pb-0 mb-8">
        <PageHeader
          title="Review Widgets"
          description="Embed review widgets on your website to showcase customer feedback"
          icon="fa-code"
          actions={
            <button onClick={() => setShowCreateModal(true)} className="px-5 py-2.5 bg-gradient-to-r from-amber-500 to-orange-500 text-white rounded-xl font-medium hover:from-amber-600 hover:to-orange-600 shadow-lg flex items-center gap-2">
              <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" /></svg>
              Create Widget
            </button>
          }
        />
        <nav className="flex items-center gap-1 overflow-x-auto -mb-px">
          {REPUTATION_TABS.map(tab => (
            <Link key={tab.id} href={tab.href} className={`px-4 py-3 text-sm font-medium border-b-2 transition-colors whitespace-nowrap ${tab.id === 'widgets' ? 'border-amber-500 text-amber-600' : 'border-transparent text-slate-500 hover:text-slate-700 hover:border-slate-300'}`}>
              {tab.label}
            </Link>
          ))}
        </nav>
      </div>

      {/* Stats */}
      <div className="grid grid-cols-2 lg:grid-cols-4 gap-4">
        <div className="bg-white rounded-xl shadow-sm border border-slate-200 p-4 text-center">
          <p className="text-2xl font-bold text-slate-900">{widgets.length}</p>
          <p className="text-sm text-slate-500">Total Widgets</p>
        </div>
        <div className="bg-white rounded-xl shadow-sm border border-slate-200 p-4 text-center">
          <p className="text-2xl font-bold text-emerald-600">{activeWidgets}</p>
          <p className="text-sm text-slate-500">Active</p>
        </div>
        <div className="bg-white rounded-xl shadow-sm border border-slate-200 p-4 text-center">
          <p className="text-2xl font-bold text-blue-600">{totalViews.toLocaleString()}</p>
          <p className="text-sm text-slate-500">Total Views</p>
        </div>
        <div className="bg-gradient-to-br from-amber-500 to-orange-500 rounded-xl shadow-sm p-4 text-center text-white">
          <p className="text-2xl font-bold">{totalClicks.toLocaleString()}</p>
          <p className="text-sm text-amber-100">Total Clicks</p>
        </div>
      </div>

      {/* Widgets Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
        {widgets.map(widget => (
          <div key={widget.id} className="bg-white rounded-2xl shadow-sm border border-slate-200 p-6 hover:shadow-md transition-shadow">
            <div className="flex items-start justify-between mb-4">
              <div className="flex items-center gap-3">
                <div className="w-12 h-12 bg-amber-50 rounded-xl flex items-center justify-center text-2xl">
                  {widget.preview}
                </div>
                <div>
                  <h3 className="font-semibold text-slate-900">{widget.name}</h3>
                  <span className="text-xs text-slate-500 capitalize">{widget.type}</span>
                </div>
              </div>
              <label className="relative inline-flex items-center cursor-pointer">
                <input type="checkbox" checked={widget.active} onChange={() => handleToggleActive(widget.id)} className="sr-only peer" />
                <div className="w-11 h-6 bg-slate-200 peer-focus:outline-none peer-focus:ring-4 peer-focus:ring-amber-300 rounded-full peer peer-checked:after:translate-x-full peer-checked:after:border-white after:content-[''] after:absolute after:top-[2px] after:left-[2px] after:bg-white after:border-slate-300 after:border after:rounded-full after:h-5 after:w-5 after:transition-all peer-checked:bg-amber-500"></div>
              </label>
            </div>

            <p className="text-sm text-slate-600 mb-4">{widget.description}</p>

            {/* Preview Area */}
            <div className="bg-slate-50 rounded-xl p-4 mb-4 text-center">
              <div className="inline-flex items-center gap-2 bg-white px-4 py-2 rounded-lg shadow-sm">
                <div className="flex items-center gap-0.5">
                  {[1, 2, 3, 4, 5].map(star => (
                    <svg key={star} className={`w-4 h-4 ${star <= 4 ? 'text-amber-400' : 'text-gray-300'}`} fill="currentColor" viewBox="0 0 20 20">
                      <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
                    </svg>
                  ))}
                </div>
                <span className="text-sm font-medium text-slate-900">4.8</span>
                <span className="text-xs text-slate-500">(127 reviews)</span>
              </div>
            </div>

            {/* Stats */}
            <div className="flex items-center justify-between text-sm text-slate-500 mb-4">
              <span>{widget.views.toLocaleString()} views</span>
              <span>{widget.clicks.toLocaleString()} clicks</span>
              <span>{((widget.clicks / widget.views) * 100).toFixed(1)}% CTR</span>
            </div>

            {/* Actions */}
            <div className="flex items-center gap-2 pt-4 border-t border-slate-100">
              <button onClick={() => handleShowCode(widget)} className="flex-1 px-3 py-2 text-sm font-medium text-amber-600 bg-amber-50 rounded-lg hover:bg-amber-100">
                Get Code
              </button>
              <button className="flex-1 px-3 py-2 text-sm font-medium text-slate-600 hover:bg-slate-50 rounded-lg">
                Customize
              </button>
              <button className="px-3 py-2 text-sm font-medium text-slate-400 hover:text-red-500 hover:bg-red-50 rounded-lg">
                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" /></svg>
              </button>
            </div>
          </div>
        ))}

        {/* Add New Widget Card */}
        <button onClick={() => setShowCreateModal(true)} className="bg-slate-50 border-2 border-dashed border-slate-300 rounded-2xl p-6 hover:border-amber-400 hover:bg-amber-50 transition-colors flex flex-col items-center justify-center min-h-[300px]">
          <div className="w-16 h-16 bg-white rounded-full flex items-center justify-center mb-4 shadow-sm">
            <svg className="w-8 h-8 text-slate-400" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" /></svg>
          </div>
          <p className="text-slate-600 font-medium">Create New Widget</p>
          <p className="text-sm text-slate-400 mt-1">Design a custom review widget</p>
        </button>
      </div>

      {/* Create Widget Modal */}
      {showCreateModal && (
        <div className="fixed inset-0 bg-black/50 backdrop-blur-sm flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-2xl max-w-md w-full shadow-2xl overflow-hidden">
            <div className="bg-gradient-to-r from-amber-500 to-orange-500 px-6 py-4 flex items-center justify-between">
              <h3 className="text-lg font-semibold text-white">Create New Widget</h3>
              <button onClick={() => setShowCreateModal(false)} className="text-white hover:bg-white/20 rounded-lg p-2">
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" /></svg>
              </button>
            </div>
            <div className="p-6 space-y-4">
              <div>
                <label className="block text-sm font-medium text-slate-700 mb-2">Widget Name</label>
                <input type="text" value={widgetName} onChange={(e) => setWidgetName(e.target.value)} placeholder="My Review Widget" className="w-full px-4 py-3 border border-slate-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-amber-500" />
              </div>
              <div>
                <label className="block text-sm font-medium text-slate-700 mb-2">Widget Type</label>
                <div className="grid grid-cols-2 gap-2">
                  {['badge', 'carousel', 'grid', 'list', 'floating'].map(type => (
                    <button key={type} onClick={() => setWidgetType(type as Widget['type'])} className={`p-3 rounded-xl border-2 text-left transition-colors ${widgetType === type ? 'border-amber-500 bg-amber-50' : 'border-slate-200 hover:border-slate-300'}`}>
                      <p className="font-medium text-slate-900 capitalize">{type}</p>
                    </button>
                  ))}
                </div>
              </div>
              <div className="flex gap-3 pt-4">
                <button onClick={() => setShowCreateModal(false)} className="flex-1 px-4 py-3 border border-slate-300 text-slate-700 rounded-xl hover:bg-slate-50 font-medium">Cancel</button>
                <button onClick={() => { setWidgets(prev => [...prev, { id: `widget-${Date.now()}`, name: widgetName || 'New Widget', type: widgetType, description: 'Custom widget', preview: '‚ú®', active: false, views: 0, clicks: 0 }]); setShowCreateModal(false); setWidgetName(''); setWidgetType('badge') }} className="flex-1 px-4 py-3 bg-gradient-to-r from-amber-500 to-orange-500 text-white rounded-xl font-medium hover:from-amber-600 hover:to-orange-600">Create Widget</button>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Embed Code Modal */}
      {showCodeModal && selectedWidget && (
        <div className="fixed inset-0 bg-black/50 backdrop-blur-sm flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-2xl max-w-2xl w-full shadow-2xl overflow-hidden">
            <div className="bg-gradient-to-r from-amber-500 to-orange-500 px-6 py-4 flex items-center justify-between">
              <h3 className="text-lg font-semibold text-white">Embed Code - {selectedWidget.name}</h3>
              <button onClick={() => { setShowCodeModal(false); setSelectedWidget(null) }} className="text-white hover:bg-white/20 rounded-lg p-2">
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" /></svg>
              </button>
            </div>
            <div className="p-6 space-y-4">
              <p className="text-sm text-slate-600">Copy and paste this code into your website where you want the widget to appear.</p>
              <div className="bg-slate-900 rounded-xl p-4 overflow-x-auto">
                <pre className="text-sm text-slate-300 font-mono whitespace-pre-wrap">{generateEmbedCode(selectedWidget)}</pre>
              </div>
              <div className="flex gap-3 pt-4">
                <button onClick={() => { setShowCodeModal(false); setSelectedWidget(null) }} className="flex-1 px-4 py-3 border border-slate-300 text-slate-700 rounded-xl hover:bg-slate-50 font-medium">Close</button>
                <button onClick={() => { navigator.clipboard.writeText(generateEmbedCode(selectedWidget)); alert('Code copied to clipboard!') }} className="flex-1 px-4 py-3 bg-gradient-to-r from-amber-500 to-orange-500 text-white rounded-xl font-medium hover:from-amber-600 hover:to-orange-600 flex items-center justify-center gap-2">
                  <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 5H6a2 2 0 00-2 2v12a2 2 0 002 2h10a2 2 0 002-2v-1M8 5a2 2 0 002 2h2a2 2 0 002-2M8 5a2 2 0 012-2h2a2 2 0 012 2m0 0h2a2 2 0 012 2v3m2 4H10m0 0l3-3m-3 3l3 3" /></svg>
                  Copy Code
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}
