// Check if an icon is an emoji (simple check for common emoji patterns)
const isEmoji = (icon: string) => {
  // Emoji regex pattern to detect emojis
  const emojiRegex = /[\u{1F300}-\u{1F9FF}]|[\u{2600}-\u{26FF}]|[\u{2700}-\u{27BF}]/u
  return emojiRegex.test(icon) || !icon.startsWith('fa-')
}

/**
 * PageHeader Component
 * 
 * Provides consistent page heading styling across all RISIVO CRM pages.
 * Includes a title, single-line description, optional icon, and action buttons.
 * 
 * Supports three icon types:
 * 1. Font Awesome icons (e.g., 'fa-folder', 'fa-bullhorn')
 * 2. SVG fallbacks for common icons (when Font Awesome fails to load)
 * 3. Emoji icons (e.g., 'üì±', 'üìß')
 * 
 * Usage:
 * ```tsx
 * <PageHeader 
 *   title="Conversations"
 *   description="Manage all your customer conversations in one place"
 *   icon="fa-comments"
 *   actions={<button>New</button>}
 * />
 * ```
 */
export function PageHeader({ 
  title, 
  description, 
  icon,
  actions,
  className = ''
}: PageHeaderProps) {
  const renderIcon = () => {
    if (!icon) return null
    
    // If it's an emoji, render directly
    if (isEmoji(icon)) {
      return <span className="text-lg">{icon}</span>
    }
    
    // Check for SVG fallback first (more reliable)
    if (SVG_ICONS[icon]) {
      return (
        <span style={{ color: 'var(--theme-primary, #3B82F6)' }}>
          {SVG_ICONS[icon]}
        </span>
      )
    }
    
    // Fallback to Font Awesome with multiple class attempts
    return (
      <>
        {/* Primary: fa-solid prefix */}
        <i className={`fa-solid ${icon} text-lg`} style={{ color: 'var(--theme-primary, #3B82F6)' }}></i>
      </>
    )
  }

  return (
    <div className={`mb-6 ${className}`}>
      <div className="flex items-start justify-between">
        <div className="flex items-center gap-3">
          {icon && (
            <div 
              className="w-10 h-10 rounded-xl flex items-center justify-center"
              style={{ backgroundColor: 'rgba(59, 130, 246, 0.1)' }}
            >
              {renderIcon()}
            </div>
          )}
          <div>
            <h1 className="text-2xl font-bold" style={{ color: 'var(--theme-text-primary, #1f2937)' }}>
              {title}
            </h1>
            <p className="text-sm mt-0.5" style={{ color: 'var(--theme-text-secondary, #6b7280)' }}>
              {description}
            </p>
          </div>
        </div>
        {actions && (
          <div className="flex items-center gap-3">
            {actions}
          </div>
        )}
      </div>
    </div>
  )
}

export default PageHeader
